import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router';
/*--------------------REGISTER BOOTSTRAP---------------------------------*/
import { BootstrapVue, BootstrapVueIcons } from "bootstrap-vue";
// Importar los archivos CSS de Bootstrap y BootstrapVue (el orden es importante)
import "bootstrap/dist/css/bootstrap.css";
import "bootstrap-vue/dist/bootstrap-vue.css";
// Haz que BootstrapVue esté disponible en todo tu proyecto
Vue.use(BootstrapVue);
// Opcionalmente, instale el plugin de componentes de iconos BootstrapVue
Vue.use(BootstrapVueIcons)
/*-----------------------------------------------------------------------*/
Vue.use(VueRouter); // Usar Vue Router

Vue.config.productionTip = false

import Main from './components/Main'
import About from './components/About'
const routes = [
  {
    path: '/',
    component: Main, // Importa y define el componente para esta ruta
  },
  {
    path: '/about',
    component: About,
  },
];

const router = new VueRouter({
  routes, // Pasa las rutas definidas al enrutador
  mode: 'history' // Opcional: Cambia el modo de enrutamiento a 'history' si deseas URLs limpias
});

new Vue({
  router,
  render: h => h(App),
}).$mount('#app')
