<template>
  <div>
    <b-navbar toggleable="lg" type="dark" variant="secondary" fixed="top">
      <b-navbar-brand href="#">
        <img rounded="circle" src="../assets/img/loro.png" width="70" alt="Circle image" />
        <label class="title-nav">Kiwi's Store</label>
      </b-navbar-brand>
      <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>
      <b-collapse id="nav-collapse" is-nav>
        <!-- Right aligned nav items -->
        <b-navbar-nav class="ml-auto">
          <b-nav-item @click="showModal" v-b-tooltip.hover.bottom
            :title="cart.length ? 'Carrito lleno' : 'Carrito vacío'">
            <b-avatar :badge="getTotalProducts()" variant="dark" icon="cart-fill" badge-variant="danger"></b-avatar>
          </b-nav-item>
        </b-navbar-nav>
      </b-collapse>
    </b-navbar>
    <b-container>
      <b-card-group deck>
        <b-row align-v="center">
          <!-- Rejilla de tarjetas de productos -->
          <b-col xs="12" sm="12" md="6" lg="4" xl="3" v-for="(item, i) in items" :key="i">
            <!-- Iteración de cards -->
            <b-card :title="`MXN $${item.price}`" header-tag="header" header-bg-variant="info" header-text-variant="white"
              :img-src="require('../assets/img/' + item.img)" img-alt="Image" img-height="200" img-top tag="article"
              border-variant="info" class="m-2">
              <template #header>
                <!-- Título de producto -->
                <h6 class="mb-0">{{ item.name }}</h6>
              </template>
              <!-- Calificación -->
              <b-icon v-for="j in 5" :key="j" font-scale="1" variant="warning" icon="star-fill"></b-icon>
              <div class="desc">
                <small>{{ item.description }}</small>
              </div>
              <b-button variant="success" size="sm" class="mb-2" pill @click="addCart(item)">
                Agregar al carrito <b-icon icon="cart-plus-fill" animation="fade" font-scale="1"></b-icon>
              </b-button>
            </b-card>
          </b-col>
        </b-row>
      </b-card-group>
    </b-container>
    <!-- Modal del carrito de compras-->
    <b-modal ref="my-modal" centered hide-footer title="Carrito">
      <div class="d-block text-center">
        <!-- Leyenda indicando que no hay productos -->
        <h5 v-if="cart.length == 0">No tienes productos en tu carrito</h5>
        <!-- Hay productos -->
        <div v-if="cart.length">
          <b-row align-v="center" v-for="prod in cart" :key="prod.id" class="m-2">
            <b-col cols="2">
              <b-avatar :src="require('../assets/img/' + prod.img)"></b-avatar>
            </b-col>
            <b-col cols="5">
              <small class="text-bold">{{ prod.name }}</small><br>
              <small>MXN ${{ prod.price }}</small>
            </b-col>
            <b-col cols="3">
              <b-button-group size="sm">
                <b-button variant="outline-danger" @click="removeCart(prod.id)">
                  <b-icon icon="dash-lg" font-scale="1"></b-icon>
                </b-button>
                <b-button disabled>{{ prod.count }}</b-button>
                <b-button variant="outline-success" @click="addCart(prod)">
                  <b-icon icon="plus-lg" font-scale="1"></b-icon>
                </b-button>
              </b-button-group>
            </b-col>
            <b-col cols="2">
              <small class="text-bold">
                ${{ Number(prod.count) * Number(prod.price) }}
              </small>
            </b-col>
          </b-row>
          <div class="text-bold text-right">
            TOTAL: ${{ getTotal() }}
          </div>
        </div>
      </div>
      <b-button pill class="mt-3" variant="outline-danger" block @click="hideModal">
        Cerrar carrito <b-icon icon="x-lg" font-scale="1"></b-icon>
      </b-button>
      <b-button pill class="mt-3" variant="outline-primary" block @click="showModalConfirm" v-if="cart.length">
        Comprar <b-icon icon="credit-card-fill" font-scale="1"></b-icon>
      </b-button>
    </b-modal>
  </div>
</template>

<script>
export default {
  data() {
    return {
      items: [
        { id: 1, description: 'Paquete con 120 bolsas biodegradables para excremento de perro.', price: 50, name: 'Pelota', img: 'pelota.jpeg' },
        { id: 2, description: 'Paquete con 120 bolsas biodegradables para excremento de perro.', price: 120, name: 'Bolsas', img: 'bolsas.jpeg' },
        { id: 3, description: 'Cama para perro, varios colores, tamaños y diseños.', price: 550, name: 'Cama para perro', img: 'cama.jpeg' },
        { id: 4, description: 'Carnaza de res para perro, sabor natural, pollo y tocino.', price: 40, name: 'Carnaza', img: 'carnaza.jpeg' },
        { id: 5, description: 'Croquetas para cachorro de raza pequeña 3.5 Kg sabor pollo y arroz.', price: 720, name: 'Comida para perro', img: 'croquetas_perro.jpeg' },
        { id: 6, description: 'Juguete con relleno, suave y resistente para perro.', price: 70, name: 'Juguete', img: 'juguete.jpeg' },
        { id: 7, description: 'Pechera para perro ajustable, varios colores y modelos.', price: 300, name: 'Pechera', img: 'pechera.jpeg' },
        { id: 8, description: 'Correa de poliéster resistente de 2.5 metros, varios colores.', price: 240, name: 'Correa', img: 'correa.jpeg' },
        { id: 9, description: 'Cepillo removedor de pelo y nudos, para peinado y limpieza.', price: 180, name: 'Cepillo', img: 'cepillo.jpeg' },
        { id: 10, description: 'Arena de sílice absorbente con cristales para control de olor 3 kg.', price: 150, name: 'Arena para gato', img: 'arena.jpeg' },
        { id: 11, description: 'Alimento seco para gatos, trucha y salmón ahumado 7.5kg.', price: 500, name: 'Comida para gato', img: 'croquetas_gato.jpeg' },
        { id: 12, description: 'Torre rascadora para gatos de 1.4 metros de altura.', price: 900, name: 'Torre rascadora', img: 'torre.jpeg' }
      ],
      cart: [],

    }
  },
  mounted() {
  },
  methods: {
    // Función para agregar al carrito
    addCart(item) {
      //Validar si el producto existe en el carrito
      let existeProduct = this.cart.filter((x) => { return x.id == item.id }).length ? true : false;
      if (existeProduct) {
        //Sumar el conteo existente del producto
        this.cart.filter(product => product.id === item.id).forEach(prod => prod.count = prod.count + 1);
      } else {
        //No existe el producto lo agregamos a la lista
        this.cart.push({ "id": item.id, "count": 1, "name": item.name, "img": item.img, "price": item.price });
      }
    },
    // Función para quitar del carrito
    removeCart(id) {
      this.cart.forEach((val, i) => {
        if ([id].includes(val.id) && val.count == 1) {
          //Si ya solo queda un producto, quitarlo de la lista
          this.cart.splice(i, 1);
        } else if ([id].includes(val.id) && val.count > 1) {
          //Si existe mas de un producto se resta 1
          this.cart[i].count = val.count - 1;
        }
      });
    },
    // Obtener total de productos
    getTotalProducts() {
      let totalProducts = 0;
      this.cart.forEach(prod => totalProducts += Number(prod.count));
      return `${totalProducts}`;
    },
    getTotal() {
      let total = 0;
      this.cart.forEach(prod => total += Number(prod.count) * Number(prod.price));
      return `${total}`;
    },
    //Mostrar modal de carrito
    showModal() {
      this.$refs['my-modal'].show()
    },
    //Ocultar modal de carrito
    hideModal() {
      this.$refs['my-modal'].hide()
    },
    //Mostrar modal de confirmación de compra
    showModalConfirm() {
      this.$bvModal.msgBoxConfirm(`¿Está seguro de comprar? El monto total es de $${this.getTotal()}.`, {
        "centered": true
      })
        .then(value => {
          if (value) {
            this.hideModal();
            this.makeToast('success');
            //Limpiamos el carrito
            this.cart = [];
          }
        })
        .catch(err => {
          // An error occurred
        })
    },
    //Mostrar alerta
    makeToast(variant = null) {
      this.$bvToast.toast('Muchas gracias por tu compra =)', {
        title: 'Compra realizada con éxito',
        variant: variant,
        solid: true
      })
    }
  }
}
</script>
